import Common from 'orion-common'
import Konva from 'konva'
import Utils from './utils'
import Element from './element'
import Text from './text'
import Comment from './comment'
import Property from './property'
import Holder from './holder'
import Htmlparser from 'htmlparser2'
/**
 * 编辑面板
 */
class FEStage {
  constructor (options) {
    this.options = {
      draggable: true,
      canZoom: true,
      canWheelZoom: true,
      readonly: false,
      debug: false,
      hoverExpand: false,
      events: {}
    }
    Common.Utils.lodash.merge(this.options, options)
    this.stage = new Konva.Stage(this.options)
    this.stage.setAttr('@stage', this)

    this.root = null

    this.layers = {}
    // 创建图层
    this.layers.model = new Konva.Layer()
    this.stage.add(this.layers.model)
    // 拖放层
    this.layers.drag = new Konva.Layer()
    this.stage.add(this.layers.drag)

    // 当前拖放的节点
    this.dragStatus = 0
    this.dragMarker = new Konva.Group({
      name: 'dragmarker',
      draggable: true,
      visible: false,
      x: 0,
      y: 0
    })
    this.layers.drag.add(this.dragMarker)

    this.holder = new Holder({
      proto: null
    })
    this.holder.knode().visible(false)
    this.layers.drag.add(this.holder.knode())

    // 是否被修改
    this.modified = false

    // 当前鼠标位置
    this.mouseClickPos = {
      x: 0,
      y: 0
    }
    this.mousePos = {
      x: 0,
      y: 0
    }

    // 是否允许缩放
    this.zoom = 1.0
    this.minZoom = 0.1
    this.maxZoom = 10
    this.zoomFactor = 1.25

    // 操作列表（用于Undo， Redo）
    this.maxAction = 50
    this.actions = []
    // 当前操作列表游标索引
    this.actionIndex = -1

    // 当前选中的节点
    this.select = null
    this.selectPos = null
    this.orderCache = {}
    this.board = null

    // focus
    this.cache = null
    let container = this.stage.container()
    // 阻止右键菜单
    container.oncontextmenu = function (event) {
      event.preventDefault()
    }
    container.tabIndex = 1
    container.focus()

    container.addEventListener('keydown', (evt) => {
      if (evt.ctrlKey === true) {
        if (this.select && this.dragStatus === 0) {
          let parent = this.select
          if (evt.key.toLowerCase() === 'c') {
            this.board = {
              repeat: true,
              cache: Common.Utils.common.clone(parent.toJson())
            }
          } else if (evt.key.toLowerCase() === 'v') {
            if (this.board && parent.nodeType() === 'element') {
              // 添加子节点或属性
              const cache = this.board.cache
              if (cache.type === 'property' && parent.canAcceptProperty()) {
                parent.addProperty({
                  key: cache.config.key,
                  value: cache.config.value
                })
                parent.adjust()
                if (!this.board.repeat) {
                  this.board = null
                }
                this.snapshot()
              } else if (cache.type !== 'property' && parent.canAcceptChild()) {
                if (cache.type === 'text') {
                  this.addText(cache.config, parent)
                } else if (cache.type === 'comment') {
                  this.addComment(cache.config, parent)
                } else {
                  this.loadElement(cache, parent)
                }
                parent.adjust({
                  bubble: true
                })
                if (!this.board.repeat) {
                  this.board = null
                }
                this.snapshot()
              }
              this.refresh()
            }
          } else if (evt.key.toLowerCase() === 'x') {
            this.board = {
              repeat: false,
              cache: Common.Utils.common.clone(parent.toJson())
            }
            this.select.deleteSelf()
          }
        }
        return
      } else if (evt.altKey === true) {
        if (evt.keyCode === 8) { // alt + backspace
          if (this.select && this.select.canClose()) {
            // 仅删除自己，将子节点添加到自己的父节点上
            let def = this.select.debrief()
            this.select = null
            this.dragStatus = 0
            this.dragMarker.setAttr('@drag', null)
            
            let list = [].concat(def.node.children)
            for (let child of list) {
              def.node.removeChild(child)
              def.parent.insertChild(child, def.pos++)
            }
            def.node.deleteSelf()
            this.root.adjust({
              capture: true
            })
          }
        }
      } else {
        const delta = 8
        if (evt.keyCode === 37) { // 左
          this.stage.x(this.stage.x() - delta)
        } else if (evt.keyCode === 38) {  // 上
          this.stage.y(this.stage.y() - delta)
        } else if (evt.keyCode === 39) {  // 右
          this.stage.x(this.stage.x() + delta)
        } else if (evt.keyCode === 40) { // 下
          this.stage.y(this.stage.y() + delta)
        } else if (this.dragStatus === 0) {
          if (evt.keyCode === 8) { // backspace
            if (this.select && this.select.canClose()) {
              this.select.deleteSelf()
            }
          } else if (evt.key === ' ') { // 空格
            if (this.select && this.select.nodeType() === 'element') {
              this.select.toggleExpand()
              this.select.refresh()
            }
          } else {
            return
          }
        }
      }
      evt.preventDefault()
      this.layers.model.batchDraw()
    })

    // 鼠标事件
    this.stage.on('mousedown', (evt) => {
      let shape = evt.target
      let node = this.findNodeParent(shape)

      if (this.select !== node) {
        if (this.select) {
          this.select.selected(false)
        }
        this.select = node
        if (this.select) {
          this.selectPos = this.stage.getPointerPosition()
          this.select.selected(true)
        }
      }
      // 判断是否可拖放
      if (this.select && this.select.canMove() && evt.evt.button === 0) {
        this.dragMarker.setAttr('@drag', this.select.debrief())
        this.dragMarker.position({
          x: 0,
          y: 0
        })
        this.dragStatus = 1
      } else {
        this.dragStatus = 0
      }

      this.options.events.select && this.options.events.select(this.select)
      this.refresh()
    })

    this.stage.on('mouseup', (evt) => {
      // 如果没有拖动，则恢复设置
      if (this.dragStatus === 1) {
        this.dragMarker.setAttr('@drag', null)
        this.dragStatus = 0
      }
    })

    this.stage.on('mousemove', (evt) => {
      this.mousePos = this.stage.getPointerPosition()
      let drag = this.dragMarker.getAttr('@drag')
      if (drag && this.dragStatus === 1 && !this.isReadOnly()) {
        this.dragMarker.startDrag()
      }
    })

    this.stage.on('dblclick', (evt) => {
      if (this.select && this.select !== this.root) {
        if (!this.isReadOnly() || this.isDebug()) {
          let elem = this.select
          let prop = ''
          if (elem.nodeType() === 'property') {
            prop = elem.key()
            elem = elem.parent()
          }
          this.options.events.edit && this.options.events.edit(elem, prop)
        }
      }
    })

    this.stage.on('dragstart', (evt) => {
      if (evt.target instanceof Konva.Stage) {
        return
      }

      this.dragStatus = 2  // 开始拖动状态
      let drag = this.dragMarker.getAttr('@drag')
      let dragtype = drag.node.nodeType()
      let position = drag.node.knode().absolutePosition()

      // 去掉stage平移变换，恢复缩放
      let ts = this.stage.getTransform()
      position.x -= ts.m[4]
      position.y -= ts.m[5]
      position.x /= ts.m[0]
      position.y /= ts.m[0]

      this.holder.setPrototype(drag.node)
      this.holder.knode().visible(true)

      drag.node.remove()
      drag.node.knode().visible(true)
      drag.node.knode().moveTo(this.layers.drag)
      drag.node.knode().position(position)

      if (drag.parent) {
        if (dragtype === 'property') {
          drag.parent.insertProperty(this.holder, drag.pos)
        } else {
          drag.parent.insertChild(this.holder, drag.pos)
        }
      }

      this.dragMarker.add(drag.node.knode())
      this.dragMarker.visible(true)

      this.refresh()
    })

    this.stage.on('dragmove', (evt) => {
     
      this.mousePos = this.stage.getPointerPosition()

      if (evt.target instanceof Konva.Stage || this.isReadOnly()) {
        return
      }
      this.dragStatus = 3  // 拖动中

      // 获取drag的绝对位置
      let drag = this.dragMarker.getAttr('@drag')
      let dragtype = drag.node.nodeType()
      let position = drag.node.knode().absolutePosition()

      let shape = this.layers.model.getIntersection(position)
      let node = this.findNodeParent(shape)
      if (!node || node === drag.node) {
        return
      }
      let nodetype = node.nodeType()

      // 进行预处理
      let drop = {
        parent: null,
        pos: -1
      }

      if (nodetype === 'property' || nodetype === 'holder') {
        drop.parent = node.parent()
        nodetype = drop.parent.nodeType()
      } else {
        drop.parent = node
      }

      if (dragtype === 'property') {
        if (drop.parent.canAcceptProperty()) {
          // 计算属性插入位置
          drop.pos = drop.parent.placeholderProperty(position)
          // 比较是否相同
          if (drag.parent === drop.parent) {
            if (drag.pos !== drop.pos) {
              drag.parent.swapProperty(drag.pos, drop.pos)
              drag.pos = drop.pos
            }
          } else { // 从旧对象删除，添加到新对象
            drag.parent.removeProperty('**holder**')
            drop.parent.insertProperty(this.holder, drop.pos)
            drag.parent = drop.parent
            drag.pos = drop.pos
            this.root.adjust({
              capture: true
            })
          }
        }
      } else if (drop.parent.canAcceptChild()) {

        if (drag.node.tagName() === 'template' && drop.parent.tagName() === 'template') {
          return
        }

        if (this.options.hoverExpand) {
          drop.parent.expand(true)
        } else if (!drop.parent.isExpanded()) {
          drop.parent = drop.parent.parent()
        }

        // 计算插入位置
        drop.pos = drop.parent.placeholderChild(position)
        // 比较是否相同
        if (drag.parent === drop.parent) {
          if (drag.pos !== drop.pos) {
            drag.parent.swapChild(drag.pos, drop.pos)
            drag.pos = drop.pos
          }
        } else { // 从旧对象删除，添加到新对象
          drag.parent.removeChild(this.holder)
          drop.parent.insertChild(this.holder, drop.pos)
          drag.parent = drop.parent
          drag.pos = drop.pos
          this.root.adjust({
            capture: true
          })
        }
      }

      this.refresh()
    })

    this.stage.on('dragend', (evt) => {
      if (evt.target instanceof Konva.Stage) {
        return
      }

      if (this.dragStatus >= 2) {

        this.dragMarker.stopDrag()
        // 执行拖放处理
        let drag = this.dragMarker.getAttr('@drag')
        let dragtype = drag.node.nodeType()
        drag.node.knode().moveTo(this.layers.model)
        if (dragtype === 'property') {
          // 替换holder
          drag.parent.removeProperty('**holder**')
          drag.parent.insertProperty(drag.node, drag.pos)
        } else {
          drag.parent.removeChild(this.holder)
          drag.parent.insertChild(drag.node, drag.pos)
        }

        this.dragMarker.setAttr('@drag', null)
        this.holder.setPrototype(null)
        this.dragStatus = 0
        this.refresh()
        this.snapshot()
      }
    })

    this.addRootElement()
    this.refresh()
    this.snapshot()
  }

  /**
   * 查找所在Group对象
   * @param shape
   * @returns {*}
   */
  findNodeParent (shape) {
    if (shape instanceof Konva.Stage) {
      return null
    }

    // 如果自己满足条件，返回自己
    let p = shape
    while (p) {
      if (p instanceof Konva.Group && p.hasName('node')) {
        break
      }
      p = p.getParent()
    }
    return p ? p.getAttr('@node') : null
  }

  /**
   * 缓冲快照
   */
  snapshot () {

    if (!this.root) {
      return
    }

    if (this.actions.length >= this.maxAction) {
      this.actions.splice(0, 1)
    }

    // 一旦添加快照，删除当前undo队列后续缓存
    if (this.actionIndex >= 0 && this.actionIndex < this.actions.length - 1) {
      this.actions.splice(this.actionIndex + 1, this.actions.length - this.actionIndex - 1)
    }

    let cache = this.saveToJson()
    this.actions.push(cache)
    this.actionIndex = this.actions.length - 1
  }

  /**
   * 刷新显示
   */
  refresh (adjust = false) {
    if (adjust && this.root) {
      this.root.adjust({
        capture: true
      })
    }

    this.layers.model.batchDraw()
    this.layers.drag.batchDraw()
  }

  updateOrder () {
    this.root.updateOrder(-1)
  }

  /**
   * 改变大小
   * @param w
   * @param h
   */
  resize (w, h) {
    w && this.stage.width(w)
    h && this.stage.height(h)
    this.stage.draw()
  }

  /**
   * 放大
   */
  zoomIn () {
    if (!this.options.canZoom) {
      return
    }
    this.zoom = Common.Utils.lodash.clamp(this.zoom * this.zoomFactor, this.minZoom, this.maxZoom)
    this.stage.scale({
      x: this.zoom,
      y: this.zoom
    })
    this.refresh()
  }

  zoomOut () {
    if (!this.options.canZoom) {
      return
    }
    this.zoom = Common.Utils.lodash.clamp(this.zoom / this.zoomFactor, this.minZoom, this.maxZoom)
    this.stage.scale({
      x: this.zoom,
      y: this.zoom
    })
    this.refresh()
  }

  reset () {
    this.zoom = 1.0
    this.stage.scale({
      x: this.zoom,
      y: this.zoom
    })
    this.stage.position({
      x: 0,
      y: 0
    })
    this.refresh()
  }

  /**
   * 获取当前选中节点，如果没有则返回根节点
   */
  selectedNode () {
    return this.select
  }

  /**
   * 获取当前选中Element
   */
  selectedElement () {
    if (!this.select) {
      return null
    }
    return this.select.nodeType() === 'element' ? this.select : null
  }

  /**
   * 清空编辑器，不能撤销
   * @param cache boolean 是否清除快照缓冲
   */
  clear (cache = true) {
    // 是否被修改
    this.modified = false


    // 清除缓存
    if (cache) {
      this.actions = []
      // 当前操作列表游标索引
      this.actionIndex = -1
    }

    this.select = null
    this.dragStatus = 0
    this.board = null

    if (this.root) {
      this.root.clearContent({
        bubble: false
      })
    }

    this.refresh()
  }

  /**
   * 添加模型
   * @param {object} data 
   * @param {object} parent 父节点 
   * {
   *  x : number
   *  y : number
   * }
   */
  addRootElement () {
    if (this.root) {
      Common.Logger.debug('Root model is already existed!')
      return
    }

    this.root = new Element({
      x: 8,
      y: 8,
      tagName: 'root',
      canMove: false,
      acceptProperty: false,
      canClose: false,
      expanded: true
    })

    this.layers.model.add(this.root.root)
    this.root.adjust()
  }
  /**
   * 添加模型
   * @param {object} data 
   * @param {object} parent 父节点 
   * {
   *  x : number
   *  y : number
   * }
   */
  addElement (data, parent) {
    if (!parent) {
      Common.Logger.debug('parent model is null!')
      return
    }

    let model = new Element(data)
    
    if (data.attributes) {
      for (let [k, v] of Object.entries(data.attributes)) {
        model.addProperty({
          name: k,
          value: v,
          event: k !== 'bind' && (k.indexOf('bind') === 0 || k.indexOf('bind:') === 0)
        })
      }
    }
    parent.addChild(model)
    model.adjust({
      bubble: true
    })

    return model
  }

  /**
   * 添加文本
   * @param {object} data 
   * @param {object} parent 父节点 
   * {
   *  x : number
   *  y : number
   * }
   */
  addText (data, parent) {
    if (!parent) {
      Common.Logger.debug('parent model is null!')
      return
    }

    let model = new Text({
      x: 16,
      y: 20,
      text: data.text
    })
    parent.addChild(model)
    parent.expand(true)
    model.adjust({
      bubble: true
    })

    return model
  }

  /**
   * 添加注释
   * @param {object} data 
   * @param {object} parent 父节点 
   * {
   *  x : number
   *  y : number
   * }
   */
  addComment (data, parent) {
    if (!parent) {
      Common.Logger.debug('parent model is null!')
      return
    }

    let model = new Comment({
      x: 16,
      y: 20,
      text: data.text
    })
    parent.addChild(model)
    parent.expand(true)
    model.adjust({
      bubble: true
    })

    return model
  }

  /**
   * 加载模型
   * @param data 快照数据
   * @param cache  boolean 是否清除快照缓冲
   */
  loadFromFile (data, cache = true) {
    // 清空面板
    this.clear(cache)

    if (!data || data.length === 0) {
      return
    }

    // 解析读取Vue template
    let current = null
    let parser = new Htmlparser.Parser({
      onopentag: (tagname, attribs) => {
        if (tagname === 'root' && current === null) {
          current = this.root
          return
        }
        current = this.addElement({
          tagName: tagname,
          attributes: attribs
        }, current)
      },
      ontext: (text) => {
        let trimed = text.replace(/(^\s*)|(\s*$)/g, '')
        if (trimed.length > 0) {
          this.addText({
            text: trimed
          }, current)
        }
      },
      oncomment: (comment) => {
        let trimed = comment.replace(/(^\s*)|(\s*$)/g, '')
        if (trimed.length > 0) {
          this.addComment({
            text: trimed
          }, current)
        }
      },
      onclosetag: (tagname) => {
        current.adjust()
        current = current.parent()
      }
    }, {
      decodeEntities: true,
      lowerCaseTags: false,
      xmlMode: true
    })
    parser.write(data)
    parser.end()

    this.root.adjust()
    this.root.expandChildren(false, true)

    // 如果清除之前缓存，则重新构建新缓存
    if (cache) {
      this.snapshot()
    }

    this.refresh()
  }

  /**
   * 
   * @param {*} json 
   */
  loadFromJson (json) {
    // 清空面板
    this.clear(false)

    if (json.children) {
      let parent = this.root
      for (let child of json.children) {
        if (child.type === 'text') {
          this.addText(child.config, parent)
        } else if (child.type === 'comment') {
          this.addComment(child.config, parent)
        } else {
          this.loadElement(child, parent)
        }
      }
    }

    this.root.adjust()
    this.refresh()
  }
  
  /**
   * 
   * @param {*} json 
   * @param {*} parent 
   */
  loadElement (json, parent) {
    let attributes = {}
    for (let prop of json.properties) {
      attributes[prop.config.name] = prop.config.value
    }

    let p = this.addElement(json.config, parent)
    for (let child of json.children) {
      if (child.type === 'text') {
        this.addText(child.config, p)
      } else if (child.type === 'comment') {
        this.addComment(child.config, p)
      } else {
        this.loadElement(child, p)
      }
    }

    return p
  }

  /**
   * 
   */
  saveToJson () {
    return this.root ? Common.Utils.common.clone(this.root.toJson()) : {}
  }

  /**
   * 保存模型
   */
  saveToFile () {
    return this.root ? this.root.code() : "<root></root>"
  }

  /**
   * 保存为Image
   */
  saveToImage () {

  }

  /**
   * 是否有undo
   * @returns {boolean}
   */
  hasUndo () {
    return this.actions.length > 1 && this.actionIndex > 0
  }

  /**
   * 撤销
   */
  undo () {
    if (this.actions.length <= 1) {
      this.actionIndex = this.actions.length - 1
      return
    }

    if (this.actionIndex < 0) {
      this.actionIndex = this.actions.length - 1
    }

    if (this.actionIndex > 0) {
      this.actionIndex -= 1
      let cache = this.actions[this.actionIndex]
      // 加载之前缓冲（保留缓冲队列）
      this.loadFromJson(cache, false)
    }
  }

  /**
   * 是否有redo
   * @returns {boolean}
   */
  hasRedo () {
    return this.actionIndex >= 0 && (this.actionIndex < this.actions.length - 1)
  }

  /**
   * 重做
   */
  redo () {
    if (this.actionIndex < 0) {
      return
    }

    if (this.actionIndex < this.actions.length - 1) {
      this.actionIndex += 1
      let cache = this.actions[this.actionIndex]
      // 加载之前缓冲（保留缓冲队列）
      this.loadFromJson(cache, false)
    }
  }

  /**
   * 
   */
  isReadOnly () {
    return this.options.readonly
  }

  isDebug () {
    return this.options.debug
  }

  /**
   * 根元素
   */
  rootElement () {
    return this.root
  }
}

export default FEStage