import Common from 'orion-common'
import wxml from './wxml'
import weapp from './weapp'
import iview from './iview'
import wux from './wux'
import minui from './minui'
import vant from './vant'

let libs = [weapp, iview, minui]
const categories = {}

const processProperty = function (k, v) {
  // 是否必须
  if (!v.title) {
    v.title = k
  }

  if (!v.desc) {
    v.desc = v.title
  }

  let dt = []
  if (Common.Utils.common.isArray(v.datatype)) {
    dt = [].concat(v.datatype)
  } else if (Common.Utils.common.isString(v.datatype)) {
    dt = v.datatype.split('|')
  }
  if (dt.length === 0) {
    dt.push('string')
  }
  v.datatype = dt

  let enums = []
  if (Common.Utils.common.isArray(v.values)) {
    enums = [].concat(v.values)
  } else if (Common.Utils.common.isString(v.values)) {
    enums = v.values.split('|')
  }
  v.values = enums

  // 缺省值默认为values的第一项或者为空
  if (!v.default) {
    v.default = v.values.length > 0 ? v.values[0] : ''
  }
  // 是否必须
  if (!v.required) {
    v.required = false
  }

  // 最小版本
  if (!v.version) {
    v.version = '*'
  }
}

const processEvent = function (k, v) {
  // 是否必须
  if (!v.desc) {
    v.desc = k
  }
  v.datatype = ['function']

  let enums = []
  if (Common.Utils.common.isArray(v.values)) {
    enums = [].concat(v.values)
  } else if (Common.Utils.common.isString(v.values)) {
    enums = v.values.split('|')
  }
  v.values = enums

  // 缺省值默认为values的第一项或者为空
  if (!v.default) {
    v.default = v.values.length > 0 ? v.values[0] : ''
  }

  // 是否必须
  if (!v.required) {
    v.required = false
  }

  // 最小版本
  if (!v.version) {
    v.version = '*'
  }
}

for (let lib of libs) {
  categories[lib.category] = lib

  if (!lib.title) {
    lib.title = lib.category
  }

  if (!lib.desc) {
    lib.desc = lib.category
  }

  if (!lib.props) {
    lib.props = {}
  }
  for (let k of Object.keys(lib.props)) {
    const v = lib.props[k]
    processProperty(k, v)
  }

  if (!lib.events) {
    lib.events = {}
  }
  for (let k of Object.keys(lib.events)) {
    const v = lib.events[k]
    processEvent(k, v)
  }

  if (!lib.tags) {
    lib.tags = {}
  }
  for (let k of Object.keys(lib.tags)) {
    const tag = lib.tags[k]
    if (!tag.title) {
      tag.title = k
    }

    if (!tag.desc) {
      tag.desc = tag.title
    }

    if (!tag.help) {
      tag.help = 'javascript:void(0)'
    }

    if (!tag.props) {
      tag.props = {}
    }
    for (let k of Object.keys(tag.props)) {
      const v = tag.props[k]
      processProperty(k, v)
    }

    if (!tag.events) {
      tag.events = {}
    }
    for (let k of Object.keys(tag.events)) {
      const v = tag.events[k]
      processEvent(k, v)
    }

    let slots = []
    if (Common.Utils.common.isArray(tag.slots)) {
      slots = [].concat(tag.slots)
    } else if (Common.Utils.common.isString(tag.slots)) {
      slots = tag.slots.split('|')
    }
    tag.slots = slots
  }
}

/**
 * 
 */
function getCategories () {
  let res = []
  for (let lib of libs) {
    res.push({
      category: lib.category,
      title: lib.title,
      desc: lib.desc
    })
  }
  return res
}

/**
 * 
 * @param {*} option 
 * key: string
 * fuzzy: boolean
 * caseSensitive: false
 * filters: [string]
 */
function search (option) {
  if (!option || !option.key) {
    return []
  }

  let res = []
  let cates = (!option.filters || (option.filters.length === 0)) ? Object.keys(categories) : option.filters
  
  if (option.fuzzy == null) {
    option.fuzzy = true
  }

  for (let c of cates) {
    let elements = categories[c]
    if (!elements || !elements.tags) {
      continue
    }

    if (!elements.props) {
      elements.props = {}
    }

    let item = {
      category: elements.category,
      title: elements.title,
      desc: elements.desc,
      elements: []
    }

    let optkey = option.caseSensitive ? option.key : option.key.toLowerCase()
    
    for (let [k, v] of Object.entries(elements.tags)) {
      let key = option.caseSensitive ? k : k.toLowerCase()
      if ((option.fuzzy && (key.indexOf(optkey) >= 0)) || key === optkey) {
        let props = Common.Utils.common.clone(elements.props || {})
        Common.Utils.lodash.merge(props, v.props || {})
        Common.Utils.lodash.merge(props, wxml.props || {})
        
        if (!v.vmodel) {
          delete props['v-model']
        }

        let events = Common.Utils.common.clone(elements.events || {})
        Common.Utils.lodash.merge(events, v.events || {})
        item.elements.push({
          name: k,
          title: v.title,
          desc: v.desc,
          help: v.help,
          props: props,
          events: events,
          slots: [].concat(v.slots)
        })
      }
    }

    if (item.elements.length > 0) {
      res.push(item)
    }
  }

  return res
}

function wxmlDefs () {
  let res = {
    tags: [],
    props: []
  }
  // 返回Tag列表
  for (let [k, v] of Object.entries(wxml.tags)) {
    res.tags.push({
      name: k,
      title: v.title || k,
      desc: v.desc || k
    })
  }

  for (let k of Object.keys(wxml.props)) {
    const v = wxml.props[k]
    processProperty(k, v)
  }

  return res
}

export default {
  getCategories,
  search,
  wxmlDefs
}
