import Common from 'orion-common'
import Konva from 'konva'
import Utils from './utils'
import Model from './model'

const _dfunc = function (config) {
  return `m 0,4 A 4,4 0 0,1 4,0 H ${ config.width - 4 } a 4,4 0 0,1 4,4 V ${config.height - 4} a 4,4 0 0,1 -4,4 H 4 a 4,4 0 0,1 -4,-4 z`
}

class Comment extends Model {
  /**
   * 
   * @param {*} config
   * tagName  string
   */
  constructor (config) {

    let elemconfig = Common.Utils.lodash.merge({
      fill: '#C1FFC1',
      stroke: '#f00',
      strokeWidth: 1,
      highlight: Utils.comment.highlight,
      dragDistance: Utils.dragDistance,
      type: 'comment',
      names: {},
      tagName: 'comment',
      tagCategory: 'wxml'
    }, config)

    elemconfig.acceptChild = false
    elemconfig.width = Math.max(config.width || 0, Utils.comment.minWidth)
    elemconfig.height = Math.max(config.height || 0, Utils.comment.minHeight)
 
    super(elemconfig)

    this.root.setAttr('@text', this)
 
    // 创建关闭按钮
    if (this.config.canClose) {
      this.components.closeBtn = this.createCloseButton({
        size: 10,
        uid: this.config.uid,
        action: () => {
          this.deleteSelf()
        }
      })
      this.root.add(this.components.closeBtn)
    }

    this.adjust()
  }

   /**
   * 事件
   * @param {} evt 
   */
  deleteSelf (evt) {
    let stage = this.stage()
    if (stage && stage.isReadOnly()) {
      return
    }
    this.remove()
    this.destroy()
    stage.refresh()
    stage.snapshot()
  }

  /**
   * 创建背景
   */
  createBackground () {
    let bg = new Konva.Path({
      x: 0,
      y: 0,
      data: _dfunc(this.config),
      fill: this.config.fill,
      stroke: this.config.stroke,
      strokeWidth: this.config.strokeWidth,
      scaleX: 1,
      scaleY: 1
    })

    bg.hitFunc((context) => {
      context.beginPath()
      context.rect(0, 0, this.config.width, this.config.height)
      context.closePath()
      context.fillStrokeShape(bg)
    })

    return bg
  }

  /**
   * 创建节点内容
   */
  createBody () {
    super.createBody()

    // Text
    this.components.text = new Konva.Text({
      x: 8,
      y: 4,
      text: this.config.text,
      fontSize: Utils.comment.fontSize,
      fontFamily: Utils.comment.fontFamily,
      fill: '#333',
      listening: false
    })
    this.body.add(this.components.text)
  }

  /*
   * 从父节点删除
   */
  remove () {
    let parent = this.parent()
    if (parent) {
      parent.removeChild(this)
    }
  }

  /**
   * 
   * @param {*} child 
   */
  removeChild (child) {
    if (!child) {
      return
    }
    let index = this.children.indexOf(child)
    if (index >= 0) {
      this.children.splice(index, 1)
      child.knode().remove()
  
      this.adjust({
        bubble: true
      })
    }
  }

  /**
   * 
   */
  debrief () {
    let res = {
      node: this,
      parent: this.parent(),
      pos: -1
    }

    if (res.parent) {
      res.pos = res.parent.children.indexOf(this)
    }

    return res
  }

  /**
   * 创建close按钮
   * @param option
   * {
   *   uid:
   *   x:
   *   y:
   *   size:
   *   background:
   *   action:
   * }
   */
  createCloseButton (option) {
    let opt = Common.Utils.lodash.merge({
      x: 0,
      y: 0,
      size: 10,
      background: 'transparent',
      stroke: '#333',
      strokeWidth: 2,
      lineCap: 'round',
      lineJoin: 'round'
    }, option)

    // 删除按钮
    let close = new Konva.Group({
      x: opt.x,
      y: opt.y,
      rotation: 45,
      name: 'node-close'
    })

    let bg = new Konva.Circle({
      x: 0,
      y: 0,
      radius: opt.size / 2,
      fill: opt.background,
      stroke: opt.background
    })
    close.add(bg)

    let lineRadius = opt.size / 2 - 2
    let hline = new Konva.Line({
      points: [-lineRadius, 0, lineRadius, 0],
      stroke: opt.stroke,
      strokeWidth: opt.strokeWidth,
      lineCap: 'round',
      lineJoin: 'round'
    })
    close.add(hline)

    let vline = new Konva.Line({
      points: [0, -lineRadius, 0, lineRadius],
      stroke: opt.stroke,
      strokeWidth: opt.strokeWidth,
      lineCap: 'round',
      lineJoin: 'round'
    })
    close.add(vline)
    close.on('mousedown', function () {
      opt.action && opt.action()
    })

    close.on('mouseout', function () {
      vline.setAttr('stroke', opt.stroke)
      hline.setAttr('stroke', opt.stroke)
      this.getLayer().draw()
    })

    close.on('mouseover', function () {
      vline.setAttr('stroke', '#FF3030')
      hline.setAttr('stroke', '#FF3030')
      this.getLayer().draw()
    })

    return close
  }

  /**
   * 调整宽度
   * @param {*} w 宽度
   */
  resizeWidth (w) {
    // 调整背景
    this.config.width = w
    this.background.data(_dfunc(this.config))

    // 调整关闭按钮
    if (this.components.closeBtn) {
      let bbox = this.components.closeBtn.getClientRect()
      // 调整close按钮
      this.components.closeBtn.position({
        x: this.config.width - bbox.width / 2,
        y: this.config.height / 2
      })
    }
  }

    /**
   * 调整宽度
   * @param {*} w 宽度
   */
  resizeHeight(h) {
    // 调整背景
    this.config.height = h
    this.background.data(_dfunc(this.config))

    // 调整关闭按钮
    if (this.components.closeBtn) {
      this.components.closeBtn.y(this.config.height / 2)
    }
  }


  /**
   * 调整布局
   */
  adjust (option) {
    let opt = Object.assign({
      bubble: false,
      capture: false
    }, option)

    //  计算属性宽度
    let textWidth = this.components.text.width() + 32
    let needWidth = Math.max(Utils.comment.minWidth, textWidth)
    this.resizeWidth(needWidth)

    let textHeight = this.components.text.height()
    let needHeight = Math.max(Utils.comment.minHeight, textHeight + 8)
    this.resizeHeight(needHeight)

    if (opt.bubble) {
      let p = this.parent()
      if (p) {
        p.adjust({
          bubble: true
        })
      }
    }
  }

  /**
   * 
   */
  tagName () {
    return this.config.tagName
  }

  /**
   * 
   */
  code (prefix = true) {
    return prefix ? `<!-- ${this.config.text} -->` : this.config.text
  }

  /**
   * 
   * @param {*} text
   */
  changeText (text) {
    this.config.text = text
    this.components.text.text(text)
    this.adjust({
      bubble: true
    })
  }

  /**
   * 
   * @param {*} adjust 
   */
  clearContent (adjust) {
    this.changeText('')
  }

}

export default Comment
